import { FC } from 'react';
import { Main1Props } from '.';
import styles from './Main1.module.scss';
import Globus from '../Globus/Globus';
import { Container } from '@mui/material';
import { Button } from '../Button';

export const Main1: FC<Main1Props> = (props) => {
	return <div className={[styles.Main1, props.className].join(' ')} {...props}>
		<Container>
		<div className={styles.content}>
			<div className={styles.bigLogoContainer}>
				<Globus className={styles.globus} />
				<img className={styles.bigLogo} src="/images/Monopolist.png" />
			</div>
			<div className={styles.cards}>
				<div className={styles.idea}>
					<span className={styles.ideaText}>
						Мы — инновационная креатив-платформа, специализирующаяся на имиджевом маркетинге и организации мероприятий, включая вечеринки, просвещение и многое другое.
					</span>
					<Button className={styles.ideaButton}>Обсудить идею</Button>
				</div>
				<div className={styles.slogan}>
					<span className={styles.sloganText}>
					ТРУДНОЕ ДОСТИГАЕТСЯ СРАЗУ, НЕВОЗМОЖНОЕ ЧУТЬ ДОЛЬШЕ, ЧУДЕСА ПО РАСПИСАНИЮ.
					</span>
				</div>
			</div>
		</div>
		</Container>
	</div>;
};
