import { FC } from 'react';
import { Main2Props } from '.';
import styles from './Main2.module.scss';
import { OwnerCard } from '../OwnerCard';
import { Button } from '../Button';
import { Container } from '@mui/system';
import useFetch from '../../hooks';
import { CircularProgress } from '@mui/material';

export const Main2: FC<Main2Props> = (props) => {
	interface result {
		picture: {
			large: string,
			medium: string,
			thumbnail: string
		}
	}

	interface picsResponse {
		results: result[]
	}

	const { data, error, pending } = useFetch<picsResponse>('https://randomuser.me/api/?results=6&inc=picture');

	return <div className={[styles.Main2, props.className].join(' ')}>
		<Container>
		<section className={[styles.section, styles.expert__section].join(' ')}>
			<article className={[styles.article, styles.expert__article].join(' ')}>
				Для экспертов и инфлюэнсеров: Мы превращаем ваше мастерство и харизму в глобальный феномен. Ваш личный бренд получит не только широкое признание, но и весомое монетизационное преимущество, благодаря нашей экосистеме поддержки и продвижения.
			</article>
			<aside className={[styles.aside, styles.expert__aside].join(' ')}>
				<div className={styles.expertCard}>
					<h4>Путь эксперта</h4>
					<img src="/images/expert.png" />
				</div>
			</aside>
		</section>
		<section className={[styles.section, styles.business__section].join(' ')}>
			<article className={[styles.article, styles.expert__article].join(' ')}>
				Для малого и среднего бизнеса: Мы ускоряем привлечение клиентов, повышаем узнаваемость и способствуем увеличению среднего чека, применяя инновационные стратегии маркетинга и организации мероприятий.			</article>
			<aside className={[styles.aside, styles.business__aside].join(' ')}>
				<OwnerCard 
				className={styles.ownerCard}
				smallHeader='Карточка собственника'
				bigHeader='Instagram магазин'
				headerColor='#D44DA0'
				/>
				<OwnerCard 
				className={styles.ownerCard}
				smallHeader='Карточка собственника'
				bigHeader='Бренд одежды'
				headerColor='#F8941E'
				/>
			</aside>
		</section>
		<section className={[styles.section, styles.partners__section].join(' ')}>
			<article className={[styles.article, styles.partners__article].join(' ')}>
				<p>Откройте для себя новые горизонты успеха. Станьте частью эксклюзивного сообщества, где ваши амбиции встречают нашу поддержку и инновации. Вместе мы создадим непревзойденную историю вашего успеха.</p>
				<Button className={styles.partnerButton}>Стать партнером</Button>
				<div className={styles.partnersWithUs}>
					<div className={styles.partnersAvatars}>
					{
					pending ? <CircularProgress /> :
						error ? <h1>Ошибка соединения</h1> :
							data?.results.map((member: result) => (
								<img className={styles.partnersAvatar} alt='avatar' key={member?.picture?.thumbnail} src={member.picture.thumbnail}  />
							))
					}
					</div>
					<span>
						30+ партнеров уже с нами
					</span>
				</div>
			</article>
			<a href="#" className={styles.isInvestor}>Являетесь инвестором? Вам <strong>сюда!</strong></a>
		</section>
		</Container>
	</div>;
};
